import { Link } from 'react-router-dom';
import { ReactComponent as Back } from '../assets/back-solid.svg';
import { ReactComponent as Home } from '../assets/home-solid.svg';
import { ReactComponent as Forward } from '../assets/forward-solid.svg';
import { lang_app } from '../lang';

function History({ history, onUndo, onRedo, lang }) {
    return (
        <div>
            <button className="el-btn" title={lang_app[lang]['back']} onClick={onUndo}
                disabled={history.current.undo.length < 1} lang-tag="back">
                <Back />
            </button>
            <button className="el-btn" title={lang_app[lang]['to_start']} lang-tag="to_start"><Home /></button>
            <div className="goto-start" tabIndex={-1}>
                <span lang-tag="go">{lang_app[lang]['go']}</span>
                <div>
                    <button className="el-btn" lang-tag="no" onClick={e => e.target.blur()}>{lang_app[lang]['no']}</button>
                    <Link className="el-btn" to="/" lang-tag="yes">{lang_app[lang]['yes']}</Link>
                </div>
            </div>
            <button className="el-btn" title={lang_app[lang]['forward']} onClick={onRedo}
                disabled={history.current.redo.length < 1} lang-tag="forward">
                <Forward />
            </button>
        </div>
    );
}
export default History;