import { useEffect, useRef } from 'react';

function Mascot({ onChange }) {
    const smw = useRef();
    const mouth = useRef();
    useEffect(() => {
        function moveSmile(e) {
            e.stopPropagation();
            const a = 45;
            const x = e.pageX;
            const y = e.pageY;
            const w = document.body.clientWidth;
            const h = document.body.clientHeight;
            const o = smw.current.getBoundingClientRect().x - (w / 2);
            const p = smw.current.getBoundingClientRect().y - (h / 2);
            let angleX = ((h / 2) - y + p + 120) / (h / 2) * a;
            let angleY = (x - o - (w / 2) - 100) / (w / 2) * a;
            if (angleX < -55) angleX = -55;
            if (angleY < -60) angleY = -60;
            smw.current.style.transform = `rotateX(${angleX}deg) rotateY(${angleY}deg)`;
        }
        document.body.addEventListener('mousemove', moveSmile);
        return () => document.body.removeEventListener('mousemove', moveSmile);
    }, []);
    if (onChange) {
        onChange.current = value => {
            if (value === true) {
                mouth.current.classList.remove('mouth-sad');
                mouth.current.classList.add('mouth-happy');
            } else if (value === false) {
                mouth.current.classList.remove('mouth-happy');
                mouth.current.classList.add('mouth-sad');
            } else {
                mouth.current.classList.remove('mouth-happy');
                mouth.current.classList.remove('mouth-sad');
            }
        }
    }
    return (
        <div className="smile">
            <div ref={smw} className="smile-wrapper" style={{ transform: 'rotateX(0deg) rotateY(0deg)' }}>
                <div>
                    <div className="eye eye-left"></div>
                    <div className="eye eye-right"></div>
                    <div className="blush blush-left"></div>
                    <div className="blush blush-right"></div>
                    <div className="mouth" ref={mouth}></div>
                </div>
            </div>
        </div>
    );
}
export default Mascot;