import { useEffect, useState } from 'react';
import { lang_app, getLangApp } from '../lang';

function Score({ onChange }) {
    const [score, setScore] = useState({ correct: 0, wrong: 0 });
    const lang = getLangApp();

    useEffect(() => {
        const correct = parseInt(localStorage.getItem('language_app_correct'));
        const wrong = parseInt(localStorage.getItem('language_app_wrong'));
        let change = false, m_score = { correct: 0, wrong: 0 };
        if (correct) { m_score.correct = correct; change = true; }
        if (wrong) { m_score.wrong = wrong; change = true; }
        if (change) setScore(m_score);
    }, []);

    onChange.current = value => {
        if (value) {
            score.correct += 1;
            localStorage.setItem('language_app_correct', score.correct);
        }
        else {
            score.wrong += 1;
            localStorage.setItem('language_app_wrong', score.wrong);
        }
        setScore({ ...score });
    };

    return (
        <>
            <div className="score" style={score.correct < 1 ? { visibility: 'hidden' } : undefined}>
                <span lang-tag="correct">{lang_app[lang]['correct']}</span> <span className="correct">{score.correct}</span>
            </div>
            <div className="score" style={score.wrong < 1 ? { visibility: 'hidden' } : undefined}>
                <span lang-tag="wrong">{lang_app[lang]['wrong']}</span> <span className="wrong">{score.wrong}</span>
            </div>
        </>
    );
}
export default Score;