import { word } from './Words';
import { sentence } from './Sentences';
import { shuffle } from '../helper/functionality';
import axiosInstance from './AxiosInstance';
const QuestionSource = { word, sentence };

export const getQuestions = () => {
    fetch('/projects/language-app/source/words.json')
        .then(res => res.json().then(val => QuestionSource.word = val).catch(err => console.log(err)))
        .catch(err => console.log(err));
    fetch('/projects/language-app/source/sentences.json')
        .then(res => res.json().then(val => QuestionSource.sentence = val).catch(err => console.log(err)))
        .catch(err => console.log(err));
};

export const getRandomQuestionService = async () => {
    const types = ['choosing', 'matching', 'sorting'];
    const type = types[Math.floor(Math.random() * types.length)];
    if (type === 'choosing') {
        const options = [], count = 4;
        const variants = ['word', 'sentence'];
        const variant = variants[Math.floor(Math.random() * variants.length)];
        let n = QuestionSource[variant].length;
        let id = Math.floor(Math.random() * n);
        let q = QuestionSource[variant][id];
        for (let i = 0; i < count; i++) {
            while (options.find(a => a.id === id)) {
                id = Math.floor(Math.random() * n);
                q = QuestionSource[variant][id];
            }
            options.push({ value: q.en, id });
        }
        return { options: shuffle(options), question: q.tr, quote: q.quo, img: q.img, type, variant, id };
    } else if (type === 'matching') {
        const qList = [], count = 6;
        let q = QuestionSource['word'][Math.floor(Math.random() * QuestionSource['word'].length)];
        for (let i = 0; i < count; i++) {
            while (qList.includes(q)) {
                q = QuestionSource['word'][Math.floor(Math.random() * QuestionSource['word'].length)];
            }
            q.pair = false;
            qList.push({ ...q });
            q.pair = true;
            qList.push(q);
        }
        return { qList: shuffle(JSON.parse(JSON.stringify(qList))), type };
    } else {
        const id = Math.floor(Math.random() * QuestionSource['sentence'].length);
        const q = QuestionSource['sentence'][id];
        return {
            question: q.tr, blanks: q.en.replaceAll(/[.!?]/g, '').replaceAll(/[^ ]/g, '_').split(' '),
            options: shuffle(q.en.toLowerCase().replace(/[.!?]/g, '').split(' ')),
            quote: q.quo, img: q.img, type, id
        };
    }
}

export const checkAnswerService = async a => {
    let result = false;
    if (a.type === 'choosing') result = a.qid === a.aid;
    else if (a.type === 'matching') result = true;
    else {
        const sentence = QuestionSource['sentence'][a.qid];
        result = sentence.en.toLowerCase().replaceAll(/[.!?]/g, '') === a.sentence;
    }
    if (result) return await getRandomQuestionService();
    return null;
}

export const findQuestion = async (type = 'choosing', variant = 'word', key = 'Walnut') => {
    const options = [], count = 4; let id = -1;
    if (type === 'choosing') {
        let q = QuestionSource[variant].find((a, i) => {
            if (a.en === key) {
                id = i;
                options.push({ value: a.en, id });
                for (let j = i + 1; options.length < count; j++)
                    options.push({ value: QuestionSource[variant][j].en, id: j });
                return true;
            }
            if (options.length < count - 1) options.push({ value: a.en, id: i });
            return false;
        });
        if (!q) { options.push({ value: 'Not found', id }); q = { tr: 'Bulunamadı' }; }
        return { options: shuffle(options), question: q.tr, quote: q.quo, img: q.img, type, variant, id };
    } else {
        let q = QuestionSource[variant].find((a, i) => {
            if (a.en === key) { id = i; return true; }
            else return false;
        });
        if (!q) { q = { en: 'Not found', tr: 'Bulunamadı' }; }
        return {
            question: q.tr, blanks: q.en.replaceAll(/[.!?]/g, '').replaceAll(/[^ ]/g, '_').split(' '),
            options: shuffle(q.en.toLowerCase().replace(/[.!?]/g, '').split(' ')),
            quote: q.quo, img: q.img, type: 'sorting', id
        };
    }
}

export const getRandomQuestion = () => axiosInstance.get('/question/random');

export const checkAnswer = data => axiosInstance.post('/question/check', data);