import { useEffect, useRef, useState } from 'react';
import { getRandomQuestionService } from '../services/Questions';
import Choosing from './Question/Choosing';
import Matching from './Question/Matching';
import Sorting from './Question/Sorting';
import Loader from './Loader/Loader';
import { getLangApp } from '../lang';
import History from './History';
import '../styles/Questions.css';

function Question({ onEmotionChange, onScoreChange }) {
    const history = useRef({ undo: [], redo: [] });
    const [question, setQuestion] = useState({});
    const lang = getLangApp();

    useEffect(() => { getRandomQuestionService().then(res => setQuestion(res)) }, []);

    const handleClick = (next, answered, forward) => {
        if (forward) setTimeout(() => handleRedo(), 1000);
        else {
            onScoreChange.current(next);
            onEmotionChange.current(!!next);
            if (next) {
                setTimeout(() => onEmotionChange.current(), 700);
                history.current.redo = [];
                if (history.current.undo.length > 50) history.current.undo.shift();
                history.current.undo.push({ ...question, answered });
                setTimeout(() => {
                    if (history.current.redo.length) history.current.redo[0] = next;
                    else setQuestion(next);
                }, 1000);
            }
        }
    }

    const handleUndo = () => {
        if (history.current.undo.length > 0) {
            history.current.redo.push(question);
            setQuestion(history.current.undo.pop());
        }
    }

    const handleRedo = () => {
        if (history.current.redo.length > 0) {
            if (history.current.undo.length > 50)
                history.current.undo.shift();
            history.current.undo.push(question);
            setQuestion(history.current.redo.pop());
        }
    }

    return (
        <div className="question-detail">
            <div><History history={history} onUndo={handleUndo} onRedo={handleRedo} lang={lang} /></div>
            {question?.type === 'choosing' ?
                <Choosing question={question} onAnswer={handleClick} lang={lang} /> :
                question?.type === 'matching' ?
                    <Matching question={question} onAnswer={handleClick} lang={lang} /> :
                    question?.type === 'sorting' ?
                        <Sorting question={question} onAnswer={handleClick} lang={lang} /> :
                        <Loader lang={lang} />
            }
        </div>
    );
}
export default Question;